import React,{Component} from 'react';

import './Box1.css';




class Box1 extends Component {
    constructor(props){
        super(props)
        this.state = {
          selectedOption1:-1,
          q1 : false,
          q2 :false,
          selectedOption2:-1,
          arr1 : [0,0],
          sum1:0,
          part1b:false,
          part1e:false,
          ans1:false,
          buttonq1 : [0,0,0,0],
          buttonq2 : [0,0,0,0],

          selectedOption3:-1,
          q3 : false,
          q4 :false,
          selectedOption4:-1,
          arr2 : [0,0],
          sum2:0,
          part2b:false,
          part2e:false,
          ans2:false,
          buttonq3 : [0,0,0,0],
          buttonq4 : [0,0,0,0],

          selectedOption5:-1,
          q5 : false,
          q6 :false,
          selectedOption6:-1,
          arr3 : [0,0],
          sum3:0,
          part3b:false,
          part3e:false,
          ans3:false,
          buttonq5 : [0,0,0,0],
          buttonq6 : [0,0,0,0],

          ques_clicked:[0,0,0,0,0,0,0,0],
          start_page:0,
          hello:0,
          dummy:-1,
        };
        this.handleOptionChange1 = this.handleOptionChange1.bind(this)
        this.handleOptionChange2 = this.handleOptionChange2.bind(this)
      //  this.handlepart1 = this.handlepart1.bind(this)
        this.handleOptionChange3 = this.handleOptionChange3.bind(this)
        this.handleOptionChange4 = this.handleOptionChange4.bind(this)
       // this.handlepart2 = this.handlepart2.bind(this)
        this.handleOptionChange5 = this.handleOptionChange5.bind(this)
        this.handleOptionChange6 = this.handleOptionChange6.bind(this)
      //  this.handlepart3 = this.handlepart3.bind(this)

        this.handleClick = this.handleClick.bind(this)
    }
    
    handleOptionChange1(event){
            this.setState({selectedOption1:event.target.value,q1:true})
            var x = event.target.value;
            let y = Number(x);
      
            let new_arr = this.state.arr1.slice();
            new_arr[0] = y;
            this.setState({arr1:new_arr})
            let new_button = [0,0,0,0];
            new_button[y] = 1;
            this.setState({buttonq1:new_button})
          
    }
    handleOptionChange2(event){
        this.setState({selectedOption2:event.target.value,q2:true})
        var x = event.target.value;
        let y = Number(x);
  
        let new_arr = this.state.arr1.slice();
        new_arr[1] = y;
        this.setState({arr1:new_arr})
        
        let new_button = [0,0,0,0];
        new_button[y] = 1;
        this.setState({buttonq2:new_button})
     
    
}
  /*  handlepart1(event){
    this.setState({ans1:true})
    if(this.state.q1 && this.state.q2){
        let s = this.state.arr1[0] + this.state.arr1[1]
        if(s>3){
            this.setState({part1b:true,part1e:false})
        }else{
            this.setState({part1b:false,part1e:true})
        }
    }
}
*/

        handleOptionChange3(event){
            this.setState({selectedOption3:event.target.value,q3:true})
            var x = event.target.value;
            let y = Number(x);

            let new_arr = this.state.arr2.slice();
            new_arr[0] = y;
            this.setState({arr2:new_arr})
            let new_button = [0,0,0,0];
            new_button[y] = 1;
            this.setState({buttonq3:new_button})
        
        }
        handleOptionChange4(event){
        this.setState({selectedOption4:event.target.value,q4:true})
        var x = event.target.value;
        let y = Number(x);

        let new_arr = this.state.arr2.slice();
        new_arr[1] = y;
        this.setState({arr2:new_arr})
        let new_button = [0,0,0,0];
        new_button[y] = 1;
        this.setState({buttonq4:new_button})

        
        }
  /*      handlepart2(event){
        this.setState({ans2:true})
        if(this.state.q3 && this.state.q4){
        let s = this.state.arr2[0] + this.state.arr2[1]
        if(s>3){
            this.setState({part2b:true,part2e:false})
        }else{
            this.setState({part2b:false,part2e:true})
        }
        }
        }  */

        handleOptionChange5(event){
            this.setState({selectedOption5:event.target.value,q5:true})
            var x = event.target.value;
            let y = Number(x);

            let new_arr = this.state.arr3.slice();
            new_arr[0] = y;
            this.setState({arr3:new_arr})
            let new_button = [0,0,0,0];
            new_button[y] = 1;
            this.setState({buttonq5:new_button})
        
        }
        handleOptionChange6(event){
        this.setState({selectedOption6:event.target.value,q6:true})
        var x = event.target.value;
        let y = Number(x);

        let new_arr = this.state.arr3.slice();
        new_arr[1] = y;
        this.setState({arr3:new_arr})
        let new_button = [0,0,0,0];
        new_button[y] = 1;
        this.setState({buttonq6:new_button})

        
        }
   /*     handlepart3(event){
        this.setState({ans3:true})
        if(this.state.q5 && this.state.q6){
        let s = this.state.arr3[0] + this.state.arr3[1]
        if(s>=2){
            this.setState({part3b:true,part3e:false})
        }else{
            this.setState({part3b:false,part3e:true})
        }
        }
        }   */


        handleClick = (ques_id,event) => {
                if(this.state.start_page==0){
                    this.setState({start_page:1})
                }else if(this.state.start_page==1 && ques_id==0){
                    this.setState({start_page:0})
                }
                let new_arr =[0,0,0,0,0,0,0,0];
                new_arr[ques_id] =1;
                this.setState({ques_clicked:new_arr})
                if(ques_id===7){
                    let s1 = this.state.arr1[0] + this.state.arr1[1];
                    let s2 = this.state.arr2[0] + this.state.arr2[1];
                    let s3 = this.state.arr3[0] + this.state.arr3[1];
                    this.setState({sum1:s1,sum2:s2,sum3:s3})
                }
             
          }



    render(){
       
        const ques= [<div className="m"  style={{marginBottom:"5%"}}>
        <br /><br />
                        <p className="intropara">With so much going, it becomes very important to pause for a few minutes to reflect on your mental well-being. This short assessment does just that. 

                            Answer 6 simple questions and get a snapshot of your current mental health. You can then take an informed decision of what your next steps could be.</p>
                        <button className="start_button" onClick ={()=> this.handleClick(1)}>Start</button>
                    </div>,
                    <div className="m">
                        <h4 className="pah">Over the last 2 weeks, how often have been bothered by the following problem?</h4>
                        <p className="pa">Little interest or pleasure in doing things<span style={{color:"red"}}>*</span>  </p>

                        <div className="container">
                            <div className="row">
                                <div className="col-md-6">
                                    {this.state.buttonq1[0]==0?<button className="unclicked_button" value="0"   onClick={this.handleOptionChange1}>Not at all</button>:<button value="0" className="clicked_button"  onClick={this.handleOptionChange1}>Not at all</button>}
                                </div>
                                <div className="col-md-6">
                                    {this.state.buttonq1[1]==0?<button className="unclicked_button" value="1"   onClick={this.handleOptionChange1}>Several Days</button>:<button value="1" className="clicked_button"  onClick={this.handleOptionChange1}>Several Days</button>}
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    {this.state.buttonq1[2]==0?<button className="unclicked_button" value="2"   onClick={this.handleOptionChange1}>More than half of the days</button>:<button value="2" className="clicked_button"  onClick={this.handleOptionChange1}>More than half of the days</button>}
                                </div>
                                <div className="col-md-6">
                                    {this.state.buttonq1[3]==0?<button className="unclicked_button" value="3"   onClick={this.handleOptionChange1}>Nearly everyday</button>:<button value="3" className="clicked_button"  onClick={this.handleOptionChange1}>Nearly everyday</button>}
                        
                                </div>
                            </div>
                            <div className="row">
                            <button className="left_button" onClick ={()=> this.handleClick(0)}>Back</button>
                            <button className="right_button" onClick ={()=> this.handleClick(2)}>Next</button>
                            </div>
                        </div>
                        
           
                        
                    </div>,
                    <div className="m">
                    <h4 className="pah">Over the last 2 weeks, how often have been bothered by the following problem?</h4>
                    <p className="pa">Feeling down, depressed or hopeless<span style={{color:"red"}}>*</span>  </p>

                        <div className="container">
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq2[0]==0?<button value="0" className="unclicked_button"  onClick={this.handleOptionChange2}>Not at all</button>:<button value="0" className="clicked_button"  onClick={this.handleOptionChange2}>Not at all</button>}
                        
                                </div>
                                <div className="col-md-6">
                                {this.state.buttonq2[1]==0?<button value="1" className="unclicked_button"  onClick={this.handleOptionChange2}>Several Days</button>:<button value="1" className="clicked_button"  onClick={this.handleOptionChange2}>Several Days</button>}
                        
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq2[2]==0?<button value="2" className="unclicked_button"  onClick={this.handleOptionChange2}>More than half of the days</button>:<button value="2" className="clicked_button"  onClick={this.handleOptionChange2}>More than half of the days</button>}
                        
                                </div>
                                <div className="col-md-6">
                                    
                                {this.state.buttonq2[3]==0?<button value="3" className="unclicked_button"  onClick={this.handleOptionChange2}>Nearly everyday</button>:<button value="3" className="clicked_button"  onClick={this.handleOptionChange2}>Nearly everyday</button>}
                        
                                </div>
                            </div>
                            <div className="row">
                            <button className="left_button" onClick ={()=> this.handleClick(1)}>Back</button>
                            <button className="right_button" onClick ={()=> this.handleClick(3)}>Next</button>
                            </div>
                        </div>


                        
                    </div>,
                    <div className="m">
                        <h4 className="pah">Over the last 2 weeks, how often have been bothered by the following problem?</h4>
                        <p className="pa">Feeling nervous. anxious or on edge<span style={{color:"red"}}>*</span>  </p>


                        <div className="container">
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq3[0]==0?<button value="0" className="unclicked_button"  onClick={this.handleOptionChange3}>Not at all</button>:<button value="0" className="clicked_button"  onClick={this.handleOptionChange3}>Not at all</button>}
                        
                                </div>
                                <div className="col-md-6">
                                    
                                {this.state.buttonq3[1]==0?<button value="1" className="unclicked_button"  onClick={this.handleOptionChange3}>Several Days</button>:<button value="1" className="clicked_button"  onClick={this.handleOptionChange3}>Several Days</button>}
                        
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq3[2]==0?<button value="2" className="unclicked_button"  onClick={this.handleOptionChange3}>More than half of the days</button>:<button value="2" className="clicked_button"  onClick={this.handleOptionChange3}>More than half of the days</button>}
 
                                </div>
                                <div className="col-md-6">
                                    
                                {this.state.buttonq3[3]==0?<button value="3" className="unclicked_button"  onClick={this.handleOptionChange3}>Nearly everyday</button>:<button value="3" className="clicked_button"  onClick={this.handleOptionChange3}>Nearly everyday</button>}
                       
                                </div>
                            </div>
                            <div className="row">
                            <button className="left_button" onClick ={()=> this.handleClick(2)}>Back</button>
                            <button className="right_button" onClick ={()=> this.handleClick(4)}>Next</button>
                            </div>
                        </div>

                  
                        
                    </div>,
                    <div className="m">
                        <h4 className="pah">Over the last 2 weeks, how often have been bothered by the following problem?</h4>
                        <p className="pa">Not being able to stop or control worrying<span style={{color:"red"}}>*</span>  </p>


                        <div className="container">
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq4[0]==0?<button value="0" className="unclicked_button"  onClick={this.handleOptionChange4}>Not at all</button>:<button value="0" className="clicked_button"  onClick={this.handleOptionChange4}>Not at all</button>}
                        
                                </div>
                                <div className="col-md-6">
                                {this.state.buttonq4[1]==0?<button value="1" className="unclicked_button"  onClick={this.handleOptionChange4}>Several Days</button>:<button value="1" className="clicked_button"  onClick={this.handleOptionChange4}>Several Days</button>}
                        
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq4[2]==0?<button value="2" className="unclicked_button"  onClick={this.handleOptionChange4}>More than half of the days</button>:<button value="2" className="clicked_button"  onClick={this.handleOptionChange4}>More than half of the days</button>}
                        
                                </div>
                                <div className="col-md-6">
                                {this.state.buttonq4[3]==0?<button value="3" className="unclicked_button"  onClick={this.handleOptionChange4}>Nearly everyday</button>:<button value="3" className="clicked_button"  onClick={this.handleOptionChange4}>Nearly everyday</button>}
                        
                        
                                </div>
                            </div>
                            <div className="row">
                            <button className="left_button" onClick ={()=> this.handleClick(3)}>Back</button>
                            <button className="right_button" onClick ={()=> this.handleClick(5)}>Next</button>
                            </div>
                        </div>

                      
                    </div>,
                    <div className="m">
                        <h4 className="pah">Over the last 2 weeks, how often have been bothered by the following problem?</h4>
                        <p className="pa">Feeling ashamed of your body<span style={{color:"red"}}>*</span>  </p>

                        <div className="container">
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq5[0]==0?<button value="0" className="unclicked_button"  onClick={this.handleOptionChange5}>Not at all</button>:<button value="0" className="clicked_button"  onClick={this.handleOptionChange5}>Not at all</button>}
                        
                                </div>
                                <div className="col-md-6">
                                {this.state.buttonq5[1]==0?<button value="1" className="unclicked_button"  onClick={this.handleOptionChange5}>Several Days</button>:<button value="1" className="clicked_button"  onClick={this.handleOptionChange5}>Several Days</button>}
                        
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq5[2]==0?<button value="2" className="unclicked_button"  onClick={this.handleOptionChange5}>More than half of the days</button>:<button value="2" className="clicked_button"  onClick={this.handleOptionChange5}>More than half of the days</button>}
                        
                                </div>
                                <div className="col-md-6">
                                {this.state.buttonq5[3]==0?<button value="3" className="unclicked_button"  onClick={this.handleOptionChange5}>Nearly everyday</button>:<button value="3" className="clicked_button"  onClick={this.handleOptionChange5}>Nearly everyday</button>}
                       
                        
                                </div>
                            </div>
                            <div className="row">
                            <button className="left_button" onClick ={()=> this.handleClick(4)}>Back</button>
                            <button className="right_button" onClick ={()=> this.handleClick(6)}>Next</button>
                            </div>
                        </div>
      
                        
                    </div>,
                    <div className="m">
                        <h4 className="pah">Over the last 2 weeks, how often have been bothered by the following problem?</h4>
                        <p className="pa">Experiencing low self-esteem<span style={{color:"red"}}>*</span>  </p>
                        <div className="container">
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq6[0]==0?<button value="0" className="unclicked_button"  onClick={this.handleOptionChange6}>Not at all</button>:<button value="0" className="clicked_button"  onClick={this.handleOptionChange6}>Not at all</button>}
                        
                                </div>
                                <div className="col-md-6">
                                {this.state.buttonq6[1]==0?<button value="1" className="unclicked_button"  onClick={this.handleOptionChange6}>Several Days</button>:<button value="1" className="clicked_button"  onClick={this.handleOptionChange6}>Several Days</button>}
                        
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                {this.state.buttonq6[2]==0?<button value="2" className="unclicked_button"  onClick={this.handleOptionChange6}>More than half of the days</button>:<button value="2" className="clicked_button"  onClick={this.handleOptionChange6}>More than half of the days</button>}
                        
                                </div>
                                <div className="col-md-6">
                                {this.state.buttonq6[3]==0?<button value="3" className="unclicked_button"  onClick={this.handleOptionChange6}>Nearly everyday</button>:<button value="3" className="clicked_button"  onClick={this.handleOptionChange6}>Nearly everyday</button>}
                        
                        
                                </div>
                            </div>
                            <div className="row">
                            <button className="left_button" onClick ={()=> this.handleClick(5)}>Back</button>
                            <button className="right_button" onClick ={()=> this.handleClick(7)}>Get Results</button>
                            </div>
                        </div>


                        
                    </div>,
                    <div className="m">
                        <h1>Result Page</h1>

                        {(this.state.sum1<=3 && this.state.sum2<=3 && this.state.sum3<2)?<div>Your responses suggest that you seem to be doing well.</div> :(this.state.sum1>3 && this.state.sum2<=3 && this.state.sum3<2)?<div>Your responses suggest that you may be dealing with symptoms of depression</div>:(this.state.sum1<=3 && this.state.sum2>3 && this.state.sum3<2)?<div>Your responses suggest that you may be dealing with symptoms of anxiety</div>:(this.state.sum1<3 && this.state.sum2<3 && this.state.sum3>=2)?<div>Your responses suggest that you may be dealing with symptoms of low self worth</div>:(this.state.sum1>3 && this.state.sum2>3 && this.state.sum3<2)?<div>Your responses suggest that you may be dealing with symptoms of depression and anxiety.</div>:(this.state.sum1>3 && this.state.sum2<3 && this.state.sum3>=2)?<div>Your responses suggest that you may be dealing with symptoms of depression and low self worth</div>:(this.state.sum1<3 && this.state.sum2>3 && this.state.sum3>=2)?<div>Your responses suggest that you may be dealing with symptoms of anxiety and low self worth.</div>:(this.state.sum1>3 && this.state.sum2>3 && this.state.sum3>=2)?<div>Your responses suggest that you may be dealing with symptoms of depression, anxiety and low self worth.</div>:null}



                        <button className="endpage" onClick ={()=> this.handleClick(6)}>Back</button>
                       
                    </div>,

        ]

       


        return(
            <div>
                <div className="d">

                    <div>
                        
                        {this.state.start_page==0?<div>{ques[0]}</div>:<div>
                        
                        {this.state.ques_clicked[1]===1?<div>{ques[1]}</div> :null}
                        {this.state.ques_clicked[2]===1?<div>{ques[2]}</div> :null}
                        {this.state.ques_clicked[3]===1?<div>{ques[3]}</div> :null}
                        {this.state.ques_clicked[4]===1?<div>{ques[4]}</div> :null}
                        {this.state.ques_clicked[5]===1?<div>{ques[5]}</div> :null}
                        {this.state.ques_clicked[6]===1?<div>{ques[6]}</div> :null}
                        {this.state.ques_clicked[7]===1?<div>{ques[7]}</div> :null}
                        <br />
                        <br />
                        <br />
                        
                        <button onClick ={()=> this.handleClick(1)}>1</button>
                        <button onClick ={()=> this.handleClick(2)}>2</button>
                        <button onClick ={()=> this.handleClick(3)}>3</button>
                        <button onClick ={()=> this.handleClick(4)}>4</button>
                        <button onClick ={()=> this.handleClick(5)}>5</button>
                        <button onClick ={()=> this.handleClick(6)}>6</button>
                        </div>}
                        
                        
                        
                       
                    </div>

                        
                        
                </div>
                <div style={{height:"300px"}}>

                </div>
            </div>
        )
    }

}

export default Box1;

